import React, { useState } from "react";
import { Navigate, useNavigate } from "react-router-dom";
import UserDataService from "../services/users";

export default function SignIn(props) {
  const [t, setType] = useState("");
  const [u, setUserName] = useState("");
  const [p, setPassword] = useState("");
  const [e, setEmail] = useState("");

  const NavPage = (t) => {
    const type = t;
    if (type == "student") {
      return "/student";
    } else if (type == "teacher") {
      return "/teacher";
    } else if (type == "manager") {
      return "/manager";
    }
  };
  //            
  let nav = useNavigate;
  const addUser = (email, password, type) => {
    const data = {
      email,
      password,
      type,
    };
    if (!email.endsWith("@ac.sce.ac.il") && !email.endsWith("@sce.ac.il")) {
      alert(
        "Please enter a valid email address ending with @ac.sce.ac.il or @sce.ac.il"
      );
      return;
    }
    const jsonData = JSON.stringify(data);
    console.log(data);
    UserDataService.createUser(data)
      .then((response) => {
        if (response.status === 200) {
          alert("you are successfuly registered");
          if (type == "student") {
            window.location = "#/student";
          } else if (type == "teacher") {
            window.location = "#/teacher";
          } else if (type == "admin") {
            window.location = "#/manager";
          }
          console.log("user added");
        } else {
          console.error("Error not added user : ", response.data.error);
        }
      })
      .catch((error) => {
        console.error("Error updating product place: ", error);
      });
  };
  return (
    <div className="form">
      <div className="form-style">
        <h1> </h1>
        <form>
          {" "}
          <input
            onChange={(e) => {
              setEmail(e.target.value);
            }}
            type="email"
            placeholder="  "
          />{" "}
          <label htmlFor="email"> : </label>
          <br />
          <br />
          <input
            onChange={(e) => {
              setUserName(e.target.value);
            }}
            type="text"
            placeholder="  /"
          />{" "}
          <label htmlFor="username"> :  </label>
          <br />
          <br />
          <input
            onChange={(e) => {
              setPassword(e.target.value);
            }}
            type="password"
            placeholder=" "
          ></input>
          <label htmlFor="password"> : </label>
          <br />
          <br />
          <select
            onChange={(e) => {
              setType(e.target.value);
            }}
          >
            <option value="please choose">please choose</option>
            <option value="student">student</option>
            <option value="admin">admin</option>
            <option value="teacher">teacher </option>
          </select>
          <label htmlFor="type"> :     </label>
          <div>
            <button
              className="buttonHome"
              onClick={() => {
                addUser(e, p, t);
              }}
            >
              
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
