import http from "../http-common";

class LoansDataService {
  getAll() {
    return http.get("/loans");
  }

  createOpinion(data) {
    return http.post("/loans", data);
  }

  updateOpinion(data) {
    return http.put(`/loans`, data);
  }

  deleteOpinion(email) {
    console.log(email);

    return http.delete(`/loans?Snumber=${email}`);
  }
}

export default new LoansDataService();
